@import url('https://fonts.googleapis.com/css2?family=Merriweather&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
.carosole{
    width: 100%;
    height: 70vh;
     

}
.wrapper{
    position: relative;
    
}
.slide{
    position: absolute;
    z-index: 5;
    width: 100%;
    height: 70vh;
}

.slide1{
    background-image: url('../../public/image/slide\ \(1\).png');
    
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.slide2{
    background-image: url('../../public/image/slide\ \(2\).png');
   
    object-fit: cover;
    background-repeat: no-repeat;
    background-size: cover;
    background-position:center;
}
.text-slide{
    position: absolute;
    top: 1%;
    left: 10%;
    font-family: 'Merriweather', serif;
    color: white;
    overflow: hidden;
}

.slidet{
    color: white;
    width: 50%;

     
   
}
.s-heading{
    font-size: 6rem;
    text-transform: capitalize;
    padding: 0.2rem;
    margin: 0.5rem 0;
    animation:heading  1s alternate ;
}
@keyframes heading{
    0%{transform: translateY(-100%);}
    100%{ transform: translateY(0%)}
}


.s-para{
    font-size: 1.2rem;
    padding: 0.5rem;
    text-align: justify;
    animation:para 2s alternate;
    font-family: 'IBM Plex Sans', sans-serif;
    color: #edf2f4;
}
@keyframes para{
    to{transform: translateX(0%);}
    from{transform: translateX(-100%);}
}

.buttonf{
    padding: 1rem 2rem;
    background: gold;
    display: inline-block;
    font-size: 1.5rem;
    margin: 1rem;
    color: black;
    cursor: pointer;
    transition: background-color 0.5s ease;
    animation: btn 2.3s alternate;
}
@keyframes btn{
    to{transform: translateY(0%);}
    from{transform: translateY(200%);}
}


.buttonf:hover{
    background-color: #4FA06E;
    color: white;
    
}
.arrowbt{
    position: absolute;
    color:#a5a58d;
    top: 10%;
    z-index: 10;
    font-size: 3rem;
    padding: 2rem;
    transform: translateY(100%);
}
.arrowbt:hover{
     
    color: #4FA06E;
    background-color: rgba(125, 125, 125, 0.5);
}
.rights{
    right: 0;
    
}


/*=============== menu =============== */

.menu-title{
text-align: center;
margin: 2rem 0;
font-size: 3rem;
text-transform: capitalize;
font-family: 'Times New Roman', Times, serif;
}

.menu-cards{
    width: 80%;
    margin: 1rem auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: hidden;
}
.card{
    width: 350px;
    height: 280px;
    margin: 1rem;
    text-align: center;
    position: relative;
    overflow: hidden;
    box-shadow: 0 0 10px 5px rgb(219, 212, 212),0px 0px 15px 15px rgb(226, 223, 223);
    /* animation: card 3s reverse; */
     
}
.card:nth-child(1){
    animation: card 2s alternate-reverse;
}
@keyframes card{
    from{transform: translateY(0%)}

    to{transform: translateY(100%)}
}
.card:nth-child(2){
    animation: card2 2s alternate-reverse;
}
@keyframes card2{
    from{transform: translateY(0%)}

    to{transform: translateY(-100%)}
}
.card:nth-child(3){
    animation: card3 2s alternate-reverse;
}
@keyframes card3{
    from{transform: translateX(0%)}

    to{transform: translateX(100%)}
}
.card:nth-child(4){
    animation: card4 2s alternate-reverse;
}
@keyframes card4{
    from{transform: translateY(0%)}

    to{transform: translateY(-100%)}
}
.card:nth-child(5){
    animation: card5 2s alternate-reverse;
}
@keyframes card5{
    from{transform: translateX(0%)}

    to{transform: translateX(-100%)}
}
.card:nth-child(6){
    animation: card6 2s alternate-reverse;
}
@keyframes card6{
    from{transform: translateY(0%)}

    to{transform: translateY(100%)}
}



.card::before{
    content: " ";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color:rgba(88, 99, 114, 0.7);
    z-index: 5;
    pointer-events: none;
    
}
.img-menu{
    width: 100%;
    height: 100%;
    object-fit:cover;
    transition: all 0.3s;
}
.img-menu:hover{
    transform: scale(1.1);
    cursor: pointer;
}
.icon-text{
    position: absolute;
    top: 0;
    transform: translate(150%,80%);
    /* pointer-events: none; */
    z-index: 6;
}
.icon{
    width: 80px;
    height: 80px;
    background-color: white;
    border-radius: 50%;
    padding: 0.2rem;
}
.icon:hover{
    
   background-color: #4FA06E;
   cursor: pointer;
    
     
}
.text{
    color: white;
    font-size: 1.5rem;
    text-transform: capitalize;
    font-family: 'Mulish', sans-serif;
}

/* ===============main =============== */

.main{
    width: 100%;
    height: 450px;
    position: relative;
    margin: 2rem 0;
}
.main::before{
    content: " ";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(25, 27, 25, 0.3);
    z-index: 5;
}
.main-img{

    width: 100%;
    height: 100%;
    object-fit: cover;
    
}
.main-text{
   
    position: absolute;
    top: 7%;
    width: 50%;
    font-family: 'Mulish', sans-serif;
    color:white;
    z-index: 6;
    right: 12rem;
    padding: 1rem;
    overflow: hidden;
}
.containt{
    font-size: 4rem;
    padding: 1rem 0;
}
.contain-artica{
    margin: 0.5rem 0;
    font-size: 1.5rem;
    
}
.btn2{
    margin: 0.5rem 0;
}
.title{
    margin: 1rem 0;
    padding: 0.5rem;
    font-size: 1rem;
    color: #4FA06E;
}

/* =======================item cards ======================= */


.item-cards{
    width: 90%;
    padding: 1rem;
     font-family: 'Times New Roman', Times, serif;
     display: flex;
     margin: 1rem auto;
     justify-content: center;
     flex-wrap: wrap;
}

.item{
    width: 220px;
    position: relative;
    margin: 1rem;
    box-shadow: 0 0 5px 5px rgb(221, 219, 219);
    
    
}
 
.item-img{
    width: 100%;
    height: 250px;
    overflow: hidden;
}
.item-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
     
}
 
.item-text{
    width: 90%;
    color: black;
    padding: 0 1rem;
}

.item-hover{
    opacity: 0;
     
    position: absolute;
    top: 0;
    background-color: rgba(125, 125, 130, 0.9);
    width: 100%;
    height: 100%;
}
.item:hover.item-hover{ 
    opacity: 1;
}
.item-btn{
    padding: 1rem;
    font-size: 1rem;
    margin: 0.5rem;
    transform: translateY(100%);
}
.container-image{
    
    background-image: url("../../public/image/slide7.jpg");
    width: 100%;
    height: 450px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 1rem;
    position: relative;
}
.container-image::before{
    position: absolute;
    content: " ";
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(125, 125, 135, 0.3);
}
.text3{
    position: absolute;
    top: 0;
    right: 1%;
    width: 50%;
    transform: translateY(30%);
    
}
.footer{
    font-family: 'Times New Roman', Times, serif;
}
 