body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  max-width: 100%;
}
.header{
  display: flex;
  padding: 5px;
  background-color: #92cbdf;
  position: relative;
  
  align-items: center;
  
}
.logo{
  padding: 0 1rem;
}
.logo1{
  width: 120px;
  height: 50px;
  object-fit: contain;
}

.menui{
  font-size: 3rem;
  padding: 2px;
  margin: 0 1rem;

}
.menui:hover{
  color: white;
  cursor: pointer;
}
.topi{
  display: none;
}


.navbar{

  display: flex;


}
.nav-list{
  list-style: none;
  margin: 0 1.5rem;
  padding: 5px;

}
.nav-link{
  text-decoration: none;
  font-size: 1.5rem;
  color: #0b2404;
  padding: 0.5rem 0;
  transition:all 0.3s ease-in-out;
}
 
.nav-link:hover{
  color: #33768f;
  border-bottom: 2px solid #d5dfe2;
}
 
 
.card-menu{
  margin: 0 2rem;
}